import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AddvehicleRoutingModule } from './addvehicle-routing.module';
import { AddvehicleComponent } from './addvehicle.component';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgbDatepickerModule, NgbModule, NgbDateAdapter, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { GlobalheaderModule } from '../../common/globalheader/globalheader.module';
import { GlobalfooterModule } from '../../common/globalfooter/globalfooter.module';
import { OwlDateTimeModule, OwlNativeDateTimeModule, OWL_DATE_TIME_LOCALE } from 'ng-pick-datetime';


@NgModule({
  declarations: [AddvehicleComponent],
  imports: [
    CommonModule,
    AddvehicleRoutingModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    NgbDatepickerModule,
    OwlDateTimeModule,
    OwlNativeDateTimeModule,
    NgbModule,
    GlobalheaderModule,
    GlobalfooterModule
  ],
  providers: [
    { provide: OWL_DATE_TIME_LOCALE, useValue: 'us' },
  ],
})
export class AddvehicleModule { }
